<template>
  <div class="video-section">
    <!-- Video Area Start -->
    <div class="video-area"  style="background-image: url('img/video.jpg')" >
      <div class="video-play-btn">
        <a class="video_btn glightbox" data-aos="fade-up">
          <i class="fa fa-play" aria-hidden="true"></i>
        </a>
      </div>
    </div>
  </div>
</template>

<script>
import  "glightbox/dist/css/glightbox.css";
import  "glightbox/dist/js/glightbox.js";
import GLightbox from 'glightbox';
export default {
  name: "VideoApp",
  data() {
   /* return {
      cssProps: {
        backgroundImage: `url(${require('/img/video.jpg')})`
      }
    }*/
  },
  mounted(){
    //lightbox settings
    this.lightbox = GLightbox({
