      'href': 'https://www.youtube.com/watch?v=MQYc4-AQJuI',
      'type': 'video',
      'source': 'youtube', //vimeo, youtube or local
      'width': 900,
      'autoPlayVideos': 'true',
    });


  }
}
</script>

<style scoped>

.video-area {
