  z-index: 1;
  background-position: center center;
  background-size: cover;
  height: 450px;
  -webkit-box-shadow: 0 10px 50px rgba(0, 0, 0, 0.08);
  box-shadow: 0 10px 50px rgba(0, 0, 0, 0.08);
  background-attachment: fixed;
}

.video-area:after {
  content: '';
