<template>
<section  class="achievements">
  <div class="container">
    <div class="row">
      <div class="col-12 text-center mb-5">
        <h2 class="ach-title">{{ $t('achievements') }}</h2>
      </div>
    </div>
    <div class="row">

      <div class="col-12 col-md-4">
        <div class="achievement-item">
          <i class="fa fa-globe"></i>
          <h5 class="counter" data-goal="12" >0</h5>
          <hr>
          <p>{{ $t('countries') }}</p>
        </div>
      </div>

      <div class="col-12 col-md-4">
        <div class="achievement-item">
          <i class="fa fa-user"></i>
          <h5 class="counter" data-goal="120">0</h5>
          <hr>
          <p>{{ $t('clients') }}</p>
        </div>
      </div>

      <div class="col-12 col-md-4">
        <div class="achievement-item">
          <i class="fa fa-smile"></i>
          <h5 class="counter" data-goal="110">0</h5>
          <hr>
          <p>{{ $t('customers') }}</p>
        </div>
      </div>

    </div>
  </div>
</section>
</template>

<script>
export default {
  name: "AchievementsApp",
  created() {
    let started = false; // Function Started ? No

    window.addEventListener("scroll", function () {
      if(started === false) {
        let statsSection = document.querySelector(".achievements");
        let nums = document.querySelectorAll(".achievement-item .counter");
        if (statsSection !== null) {
          if ((window.scrollY + 300) >= statsSection.offsetTop) {
            if (!started) {
              nums.forEach((num) => startCount(num));
            }
            started = true;
          }

        }
      }
    });
    function startCount(el) {
      let goal = el.dataset.goal;
      let count = setInterval(() => {
        el.textContent++;
        if (el.textContent == goal) {
          clearInterval(count);
        }
      }, 10);
    }


  }
}
</script>

<style scoped>
.achievements{
  padding-top: 50px;
  padding-bottom: 50px;
}

.ach-title{
  text-align: center;
  padding-bottom: 35px;
  position: relative;
  display: inline-block;
  font-weight: 600;
  font-size: 24px;
  margin: 0;
  text-transform: uppercase;

}

.ach-title::before {
  content: "";
  background: var(--main-color1);
  width: 70px;
  height: 1px;
  position: absolute;
  left: 0px;
  right: 0px;
  bottom: 10px;
  margin: auto;
}
.ach-title:after {
  content: "";
  background: var(--main-color1);
  width: 40px;
  height: 1px;
  position: absolute;
  left: 0px;
  right: 0px;
  bottom: 0px;
  margin: auto;
}


.achievement-item{
  display: flex;
  flex-direction: column;
  justify-content: center;
  justify-items: center;
  text-align: center;
  align-items: center;
  margin-bottom: 20px;
  box-shadow: 0px 0px 5px 1px #636d7555;
  padding: 20px;
  background-color: #efe2fc;
  color: var(--main-color1);
  border-top-left-radius: 50px;
  border-bottom-right-radius: 50px;
}

.achievement-item i{
  font-size: 50px;
  margin-bottom: 20px;
}
.achievement-item h5{
  font-size: 30px;
  font-weight: bolder;
}



.achievement-item hr{
  width: 50%;
}
.achievement-item p{
  font-size: 20px;
  font-weight: bolder;
}
</style>