<template>
  <div id="app-header">
    <header class="main-header">
      <nav class="navbar navbar-expand-lg bg-transparent">
        <div class="container">
          <a class="navbar-brand" href="#">
            <img src="Logo.svg" width="40" height="40">
            {{ $t('brand') }}
          </a>
          <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span class="navbar-toggler-icon"></span>
          </button>
          <div class="collapse navbar-collapse" id="navbarSupportedContent">
            <ul class="navbar-nav ms-auto me-auto mb-2 mb-lg-0">
              <li class="nav-item">
                <a class="nav-link active" aria-current="page" href="/">{{ $t('home') }}</a>
              </li>
              <li class="nav-item">
                <a class="nav-link" href="#insights">{{ $t('services') }}</a>
              </li>


              <li class="nav-item dropdown menu-large dropdown-hover">
                <a class="nav-link dropdown-toggle" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                  {{ $t('products') }}
                </a>
                <ul class="dropdown-menu megamenu container-fluid">
                    <li class="row">
                    <div class="col-12 col-md-4">
                      <div class="box-link">
                        <h5>{{ $t('products') }}</h5>
                        <ul>
                          <li><a href="#stack" @mouseover="hoverProduct($event)" class="navmega-item">Stack</a></li>
                          <li><a href="#pipelines" @mouseover="hoverProduct($event)" class="navmega-item">Pipelines</a></li>
                        </ul>
                      </div>
                    </div>

                    <div class="col-12 col-md-4">
                      <div class="product-box">
                        <h2>{{ $t(this.title_pro) }}</h2>
                        <p >{{ $t(this.dis_pro) }}</p>
                      </div>
                    </div>
                    <div class="col-12 col-md-4">
                      <div class="box-image" >
                        <img :src="this.url_img" class="image-mega">
                      </div>
                    </div>
                  </li>
                </ul>
              </li>

              <li class="nav-item">
                <a class="nav-link" href="#PricingPlans">{{ $t('prices') }}</a>
              </li>
              <li class="nav-item">
                <a class="nav-link" href="#contact">{{ $t('contact') }}</a>
              </li>
            </ul>
            <ul class="navbar-nav  mb-2 mb-lg-0">
              <li class="nav-item dropdown ">
                <a class="nav-link dropdown-toggle" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                  <img :src="'lang/'+this.lang+'.svg'" class="rounded-circle" width="25" height="25"> {{$t('language')}}
                </a>
                <ul class="dropdown-menu">
                  <li><a @click="changeLanguage('en')" class="dropdown-item" href="#">
                    <img class="rounded-circle me-1 ms-1" src="lang/en.svg" width="25" height="25">{{$t('english')}}</a></li>
                  <li><a @click="changeLanguage('ar')" class="dropdown-item" href="#">
                    <img class="rounded-circle me-1 ms-1" src="lang/ar.svg" width="25" height="25">{{$t('arabic')}}</a></li>
                </ul>
              </li>
              <li class="nav-item">
                <button class="btn btn-started" type="submit">{{$t('started')}}</button>
              </li>

            </ul>

          </div>
        </div>
      </nav>
    </header>
  </div>
</template>

<script>
window.$ = window.jQuery = require('jquery');
export default {
  name: "HeaderApp",
  created () {
    window.addEventListener('scroll', this.handleScroll);
    if(this.$i18n.locale.includes("en")===true){
      this.lang="en"
    }else{
      this.lang="ar"
    }

  }, data(){
    return{checked:true,isScrolled:false,
      url_img:'img/stack.png',
      title_pro:"stack",
      dis_pro:"stack_des",
      lang:"en"
    };

  },methods:{
    handleScroll () {
      if(window.scrollY > 10){
        window.$('.main-header').addClass('sticky slideInDown');
      }else {
        window.$('.main-header').removeClass('sticky slideInDown');
      }
    },
    changeLanguage(lang){
      window.location.reload();
      localStorage.setItem('lang',lang);
      this.$i18n.locale=lang;
      document.documentElement.setAttribute("lang", lang);
    },
    hoverProduct(el){
      let text=el.target.innerHTML;
      switch (text){
        case "Stack":
          this.url_img='img/stack.png';
          this.title_pro="stack";
          this.dis_pro="stack_des";
          break;
        case "Pipelines":
          this.url_img='img/pipelines.png';
          this.title_pro="pipelines";
          this.dis_pro="pipelines_des";
          break;

          default:
            this.url_img='img/stack.png';
            this.title_pro="stack";
            this.dis_pro="stack_des";
            break;
      }
    }
  }

}
</script>

