import { createApp } from 'vue'
import App from './App.vue'
import 'fontawesome-4.7/css/font-awesome.min.css'
import '@fortawesome/fontawesome-free/css/fontawesome.min.css'
import AOS from 'aos';
import 'aos/dist/aos.css';
